/* Form layout */
.iframewidget-grid-form[data-v-08277b8a] {
    display: grid;
    margin-top: 30px;
}
.iframewidget-grid-form label[data-v-08277b8a]:not(:first-child) {
    margin-top: 20px;
}
input[data-v-08277b8a] {
    max-width: 400px;
    width: 100%;
}

/* Checkbox styles */
.checkbox-container[data-v-08277b8a] {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.checkbox-container input[type="checkbox"][data-v-08277b8a] {
    position: static;
    display: block;
    height: 16px;
    width: 16px;
    margin: 0;
    opacity: 1;
    cursor: pointer;
}
.checkbox-container .checkbox-icon[data-v-08277b8a] {
    display: none;
}
.checkbox-container input[type="checkbox"][data-v-08277b8a]:checked {
    background-color: var(--color-primary);
    border-color: var(--color-primary);
}

/* Button styles */
.iframewidget-button-group[data-v-08277b8a] {
    margin-top: 20px;
}

/* Header styles */
.iframewidget-header[data-v-08277b8a] {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 10px;
}
.iframewidget-logo[data-v-08277b8a] {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.iframewidget-logo a[data-v-08277b8a] {
    text-decoration: none;
    color: inherit;
}
.iframewidget-logo a.logo-link[data-v-08277b8a] {
    display: block;
}
.iframewidget-logo a:hover .iframewidget-version[data-v-08277b8a] {
    text-decoration: underline;
    color: var(--color-primary-element);
}
.iframewidget-logo-image[data-v-08277b8a] {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.iframewidget-logo-image[data-v-08277b8a]:hover {
    transform: scale(1.05);
    filter: drop-shadow(1px -1px 3px rgba(0, 0, 0, 0.5));
}
.iframewidget-version[data-v-08277b8a] {
    font-size: 12px;
    color: var(--color-text-maxcontrast);
    margin-top: 5px;
}

/* Layout container */
.iframewidget-admin-container[data-v-08277b8a] {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
}
.iframewidget-admin-form[data-v-08277b8a] {
    flex: 0.5;
    min-width: 330px;
}
.iframewidget-admin-preview[data-v-08277b8a] {
    flex: 1;
    min-width: 320px;
    max-width: 640px;
}

/* Preview styles */
.preview-container[data-v-08277b8a] {
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius-container-large);
    overflow: hidden;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    background-color: var(--color-main-background-blur);
    transition: width 0.3s ease;
}
.preview-header[data-v-08277b8a] {
    display: flex;
    align-items: center;
    padding: 16px;
    background-color: var(--color-main-background-blur);
}
.preview-header h2[data-v-08277b8a] {
    display: block;
    align-items: center;
    flex-grow: 1;
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    padding: 16px 8px;
    height: 56px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.preview-header span[data-v-08277b8a]:first-child {
    width: 16px;
    height: 16px;
    background-size: 16px;
    margin-right: 8px;
}
.preview-header span[data-v-08277b8a] {
    background-size: 32px!important;
    width: 32px!important;
    height: 32px!important;
    background-position: center;
    float: left;
    margin-top: -6px;
}
.preview-content[data-v-08277b8a] {
    margin: 0 16px 16px 16px;
    position: relative;
    height: 424px;
}
.preview-empty[data-v-08277b8a] {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    padding: 20px;
    color: var(--color-text-maxcontrast);
    text-align: center;
}
.preview-frame[data-v-08277b8a] {
    width: 100%;
    height: 100%;
    border: none;
    min-height: 50px;
}
.preview-title[data-v-08277b8a] {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 10px;
    margin-top: 0;
}

/* Empty title styles */
.preview-header.preview-title-empty[data-v-08277b8a] {
    height: 10px !important;
    padding: 0 !important;
}
.preview-header.preview-title-empty h2[data-v-08277b8a] {
    display: none !important;
}
.preview-content.preview-title-empty[data-v-08277b8a] {
    height: calc(424px + 78px);
}

/* Custom icon styles */
.preview-header h2 .widget-icon[data-v-08277b8a] {
    float: left !important;
    display: inline-flex !important;
    margin-right: 16px !important;
    margin-left: 0 !important;
    position: relative !important;
    width: 32px !important;
    height: 32px !important;
}
.preview-header h2 .widget-icon img[data-v-08277b8a] {
    width: 32px;
    height: 32px;
    object-fit: contain;
    vertical-align: middle;
}

/* Hide default icon when custom icon is present */
.preview-header h2 .widget-icon + .icon-iframewidget[data-v-08277b8a] {
    display: none !important;
}

/* Icon input styles */
.icon-input-container[data-v-08277b8a] {
    display: flex;
    align-items: center;
    gap: 15px;
    max-width: 400px!important;
}

/* Icon Finder Link styles */
.icon-finder[data-v-08277b8a] {
    font-size: 0.85em;
    font-weight: normal;
    color: var(--color-text-maxcontrast);
}
.icon-finder a[data-v-08277b8a] {
    color: var(--color-primary);
}

/* Color button container transitions */
.color-button-container[data-v-08277b8a] {
    display: inline-flex;
    transition: width 0.3s ease, margin 0.3s ease;
    width: 0;
    height: 32px;
}
.color-button-container.has-button[data-v-08277b8a] {
    width: 32px;
    margin-left: 4px;
}

/* Color picker styles */
input[type="color"][data-v-08277b8a] {
    aspect-ratio: 1/1;
    min-width: 32px;
    min-height: 32px;
    width: 32px;
    height: 32px;
    padding: 0!important;
    margin: 0!important;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5) !important;
    transition: transform 0.2s, box-shadow 0.2s;
	cursor: pointer!important;
}
input[type="color"][data-v-08277b8a]:hover {
    transform: scale(1.1);
    transition: transform 0.2s, box-shadow 0.2s;
    box-shadow: 0 0 12px rgba(255, 255, 255, 0.5) !important;
}
input[type="color"][data-v-08277b8a]::-webkit-color-swatch-wrapper {
    padding: 0;
}
input[type="color"][data-v-08277b8a]::-webkit-color-swatch {
    border: none;
    border-radius: 50%;
}

/* Firefox support */
input[type="color"][data-v-08277b8a]::-moz-color-swatch {
    border: none;
    border-radius: 50%;
}

/* Color reset button transitions */
.icon-reset-color[data-v-08277b8a] {
    min-width: 32px;
    min-height: 32px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    opacity: 0.7;
    margin: 0!important;
    border-radius: 50%;
}
.icon-reset-color[data-v-08277b8a]:hover {
    opacity: 1;
    background-color: var(--color-background-hover);
}
.fade-scale-enter-from[data-v-08277b8a] {
    opacity: 0;
    transform: scale(0);
}
.fade-scale-enter-active[data-v-08277b8a], 
.fade-scale-leave-active[data-v-08277b8a] {
    transition: all 0.3s ease;
}
.fade-scale-enter-from[data-v-08277b8a],
.fade-scale-leave-to[data-v-08277b8a] {
    opacity: 0;
    transform: scale(0);
}
.icon-preview[data-v-08277b8a] {
    display: inline-flex;
    align-items: center;
    margin: 0 4px;
}
#widget-icon[data-v-08277b8a] {
    max-width: 350px!important;
}

/* CSP error display styling */
.widget-error.csp-error[data-v-08277b8a] {
    display: flex;
    flex-direction: column;
    padding: 16px;
    text-align: center;
    height: 200px;
    justify-content: center;
}

/* Error title styling */
.widget-error .error-title[data-v-08277b8a] {
    font-weight: bold;
    margin-bottom: 8px;
    color: var(--color-text-maxcontrast);
}

/* Error message paragraph styling */
.widget-error p[data-v-08277b8a] {
    margin: 8px 0;
    color: var(--color-text-maxcontrast);
}

/* Container for action buttons in error state */
.error-actions[data-v-08277b8a] {
    margin-top: 16px;
}

/* Button styling for error actions */
.error-actions .button[data-v-08277b8a] {
    display: inline-block;
    padding: 8px 16px;
    text-decoration: none;
    border-radius: var(--border-radius);
    background-color: var(--color-primary);
    color: var(--color-primary-text);
}

/* Hover state for action buttons */
.error-actions .button[data-v-08277b8a]:hover {
    background-color: var(--color-primary-element-hover);
}

